@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=B612+Mono&display=swap);
:root {
  --primary-cl-07: rgba(40, 143, 159, 0.7);
  --primary-cl-03: rgba(40, 143, 159, 0.3);
  --secondary-card-cl-07: rgba(88, 88, 88, 0.7);
  --bg-cl: rgba(255, 255, 255, 0.015);
  --white-cl-10: hsla(0, 100%, 100%, 1);
  --white-cl-05: hsla(0, 100%, 100%, 0.5);
  --white-cl-02: hsla(0, 100%, 100%, 0.3);
  --alert-cl: hsl(16, 100%, 50%);
}

html {
  font-size: 82.5%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  color: #fff;
  /* overflow-x: hidden; */
}

h1,
h2,
h3 {
  font-weight: 400;
  margin: 0.5rem 0;
}

h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.2rem;
  text-transform: capitalize;
}
p {
  margin: 0;
  padding: 0;
}

#root {
  width: 100vw;
  min-height: 100vh;

  background: url(/weather-app/static/media/background-image.e3fada3a.jpg);
  background-size: cover;
  background-position: center;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 3rem 0;
}

.main-content-wp {
  display: flex;
  flex-direction: column;

  min-width: 75vw;

  min-height: 100vh;

  background: rgba(255, 255, 255, 0.015);

  background: var(--bg-cl);
  padding: 3rem;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  box-shadow: 15px 15px 10px #0000;
}

.icon {
  font-size: 2.5rem;
  color: hsl(16, 100%, 50%);
  color: var(--alert-cl);
}

/* Media queries */

@media only screen and (max-width: 1530px) {
  html {
    font-size: 70%;
  }
  section {
    width: 75vw;
  }
}

@media only screen and (max-width: 1024px) {
  html {
    font-size: 60%;
  }
  section {
    width: 95vw;
  }
}

@media only screen and (max-width: 860px) {
  .container {
    height: 100%;
  }
  html {
    font-size: 58%;
  }
}

@media only screen and (max-width: 720px) {
  html {
    font-size: 55%;
  }
}

@media only screen and (max-width: 640px) {
  .main-content-wp {
    width: 100vw;
    padding: 5rem 0;
  }
}

.searchLocationContainer {
  width: 30vw;
  padding: 8rem;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  box-shadow: 15px 15px 10px #0000;
  text-align: center;
}

.searchLocationContainer .searchInputForm {
  justify-content: center;
  margin-top: 3rem;
}

.searchLocationContainer .searchInputForm input {
  width: 100%;
}

.searchLocationContainer h3 {
  text-transform: none;
}

.searchLocationContainer p {
  text-align: start;
}

.searchInputForm {
  display: flex;
  margin-left: auto;
}

.searchInput {
  border: none;
  outline: none;
  background-color: var(--primary-cl-03);

  border-radius: 8px 0 0 8px;
  padding: 1em 1.5em;
  color: var(--white-cl-10);

  transition: all 0.2s ease-out;
}

.searchInput.invalid {
  outline: 2px solid var(--alert-cl);
}

.searchInput::-webkit-input-placeholder {
  color: var(--white-cl-05);
}

.searchInput:-ms-input-placeholder {
  color: var(--white-cl-05);
}

.searchInput::placeholder {
  color: var(--white-cl-05);
}

.searchInputBtn {
  outline: none;
  border: none;
  padding: 0 1.5em;
  background-color: var(--primary-cl-07);
  border-radius: 0 8px 8px 0;
  color: var(--white-cl-05);

  transition: color 0.2s ease-in;
  cursor: pointer;
}

.searchInputBtn:hover {
  color: var(--white-cl-10);
}

.message {
  position: absolute;

  left: 0;
  bottom: -2rem;
  color: red;
  padding: 0.5rem;
  text-align: start;
  transition: all 0.2s ease-out;
}

@media only screen and (max-width: 640px) {
  .searchInputForm {
    justify-content: center;
  }
}

.current-info_location {
  display: flex;
  flex-direction: column;
}

.current-info_data {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin: 3rem 0;
}

.clock-wp {
  font-family: "B612 Mono", monospace;
  flex: 1 1;
  margin: 0 auto;
  text-align: center;
}

.current-weather-info-wp {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.current-weather-info_description {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.current-weather-info_description i {
  padding: 1.3rem 0;
}

.current-weather-info {
  line-height: 2.5rem;
}

hr {
  width: 6rem;
  margin: 0;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  color: var(--white-cl-02);
}

.location-wp {
  display: flex;
  margin: 3rem auto;
}

.location-city {
  text-align: center;
  min-width: 17ch;
  margin: 0 2rem;
  font-size: 3rem;
}

.location-btn {
  border: 2px solid hsla(0, 100%, 100%, 0.3);
  border-radius: 50%;
  background-color: transparent;

  cursor: pointer;

  margin: auto 0;

  transition: border 0.2s ease;
}

.location-btn:hover {
  border: 2px solid hsla(0, 100%, 100%, 0.6);
}

.location-btn:hover .icon {
  color: hsla(0, 100%, 100%, 0.8);
}

.arrow-icon {
  font-size: 2rem;
  padding: 0.2em;
  color: hsla(0, 100%, 100%, 0.3);

  transition: color 0.2s ease-in-out;
}

.daily-forecast-wp {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;

  grid-gap: 2rem;

  gap: 2rem;
}

.daily-forecast_card {
  flex: 0 1 8rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: linear-gradient(
    -45deg,
    var(--primary-cl-07),
    var(--secondary-card-cl-07) 80%
  );
  margin-top: 3rem;
  padding: 1rem;
}

.daily-forecast_card-temp {
  font-size: 2rem;
}

.daily-forecast_card-temp span {
  font-size: 1.5rem;

  color: #b4b4b4;
}

.daily-forecast_card .icon {
  padding: 1rem 0;
}

@media only screen and (max-width: 640px) {
  .daily-forecast-wp {
    flex-direction: column;
    grid-gap: 0;
    gap: 0;
  }

  .daily-forecast_card {
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0 2rem;
  }

  .daily-forecast_card-description {
    margin: 0 2rem 0 auto;

    width: 7ch;
    text-overflow: ellipsis;
    white-space: wrap;
  }

  .daily-forecast_card-temp {
    width: 5ch;
  }
}

.hourly-forecast {
  margin-top: 3rem;
}

.hourly-forecast h2 {
  text-align: center;
}

.hourly-forecast_chart {
  max-width: 90%;
  height: 24rem;
  margin: 0 auto;
}

.loading-wp {
  position: relative;
  flex-grow: 1;
  align-items: center;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cloud-icon {
  position: relative;
  display: block;
  width: 12em;
  height: 10em;
  font-size: 1em;
}

.cloud {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 3.6875em;
  height: 3.6875em;
  margin: -1.84375em;
  background: currentColor;
  border-radius: 50%;
  box-shadow: -2.1875em 0.6875em 0 -0.6875em, 2.0625em 0.9375em 0 -0.9375em,
    0 0 0 0.375em #fff, -2.1875em 0.6875em 0 -0.3125em #fff,
    2.0625em 0.9375em 0 -0.5625em #fff;
}
.cloud:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -0.5em;
  display: block;
  width: 4.5625em;
  height: 1em;
  background: currentColor;
  box-shadow: 0 0.4375em 0 -0.0625em #fff;
}
.cloud:nth-child(2) {
  z-index: 0;
  background: #fff;
  box-shadow: -2.1875em 0.6875em 0 -0.6875em #fff,
    2.0625em 0.9375em 0 -0.9375em #fff, 0 0 0 0.375em #fff,
    -2.1875em 0.6875em 0 -0.3125em #fff, 2.0625em 0.9375em 0 -0.5625em #fff;
  opacity: 0.3;
  -webkit-transform: scale(0.5) translate(6em, -3em);
          transform: scale(0.5) translate(6em, -3em);
  -webkit-animation: cloud 4s linear infinite;
          animation: cloud 4s linear infinite;
}
.cloud:nth-child(2):after {
  background: #fff;
}

@-webkit-keyframes cloud {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(0.5) translate(-200%, -3em);
            transform: scale(0.5) translate(-200%, -3em);
  }
}

@keyframes cloud {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(0.5) translate(-200%, -3em);
            transform: scale(0.5) translate(-200%, -3em);
  }
}

